@import "../../widgits/css/general.scss";
main{
    @extend %main;
}
.main_fixed{
    margin-top: 90px
}
.userHtml{
    padding: 0 calcFluid(8, 200, 320, 1920) !important;
    @extend %d-col;
    h2{font-size: calcFluid(14, 27, 320, 1920);}
    .orders__info {
        width: 100%;
        @extend %d-col;
        .input__price,
        .input__delivery,
        .input__discount {
            @extend %d-row;
            margin: 10px 0;
            font-size: calcFluid(15, 20, 320, 1920);
            font-weight: 900;
            p {margin: 10px 5px 0 0;}
            #price {
                margin: 0 0 0 auto;
                font-size: calcFluid(17, 30, 320, 1920);
                font-weight: 900;
            }  
            span {
                margin: 0 0 0 auto !important;
                font-size: calcFluid(13, 20, 320, 1920);
                font-weight: 900;
            }              
        }
       
        .input__delivery {
            margin: 0;
        }
        .input__discount{
            p {margin: 5px 5px 0 0;}
            #discount {
                font-size: calcFluid(13, 22, 320, 1920);
                color: #f91155;
            }
        }
        .orders__track{
            box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.25);
            @extend %d-col;
            padding: calcFluid(10, 18, 320, 1920) calcFluid(10, 18, 320, 1920) calcFluid(20, 30, 320, 1920) calcFluid(10, 18, 320, 1920) ;
            .orders__trackInfo{
                @extend %d-row;
                justify-content: center;
                .orders__trackInfoCol, 
                .orders__trackInfoCol1{
                    position: relative;
                    margin: 0 auto;
                    @extend %d-col;
                    p{  
                        text-align: center;
                        margin: 0;
                        font-weight: 900;
                        font-size: calcFluid(12, 20, 320, 1920);
                    }
                    .date{
                        margin: 5px 0 calcFluid(5, 25, 320, 1920) 0;
                        font-weight: 500;
                        color: #58585e;
                    }
                    .borderRadius{
                        z-index: 100;
                        border-radius: 50%;
                        background: gray;
                        width: calcFluid(30, 42, 320, 1920);
                        height: calcFluid(30, 42, 320, 1920);
                        margin: 0 auto;
                        top: calcFluid(-10, -20, 320, 1920);
                        img{
                            margin: calcFluid(6, 8, 320, 1920);
                            width: 60%;
                        }
                    }
                    .active{
                        background: $gradient;
                    }
                    .border{
                        position: absolute;
                        border: 2px solid gray;
                        width: calcFluid(100, 330, 320, 1920);
                        top: calcFluid(55, 100, 320, 1920);
                        left: calcFluid(30, 70, 320, 1920);
                    }
                    
                    .borderActive{
                        border: 2px solid gray !important;
                        border-image-source: linear-gradient(90deg, #5134c4, #773cbc, #b856d4) !important;
                        border-image-slice: 1 !important;
                        background: linear-gradient(90deg, #5134c4, #773cbc, #b856d4);
                        -webkit-background-clip: text;
                        background-clip: text;
                        -webkit-text-fill-color: transparent;
                    }
                }
                .orders__trackInfoCol1{
                    .border{
                        width: calcFluid(80, 300, 320, 1920);
                    }
                }
            }
        }
        .writeToShop{
            @extend %more;
            margin:15px calcFluid(0, 220, 320, 1920) 0 calcFluid(0, 220, 320, 1920);
        }
    }
    .bag__block {
        margin: 10px 0;
        @extend %d-row;
        box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.25);
        padding: 10px;
        text-decoration: none;
        .block__img, 
        .block__imgJS {
            max-width: 30%;
            margin: auto;
            img {
                max-width: 90%;
            }
        }
        .block__imgJS{
            max-width: 25%;
            img {
                max-width: 100%;
            }
        }

        .block__content {
            position: relative;
            margin: 0 0 0 10px;
            width: 70%;
            @extend %d-col;
            color: black;
            .rent {
                color: #58585e;
                font-weight: 500;
                margin: 0 0 auto 0;
                font-size: calcFluid(12, 18, 320, 1920);
                span{
                    color: black;
                    font-weight: 1000;
                }
            }

            .orderNumber {
                text-decoration: none;
                color: black;
                font-size: calcFluid(15, 25, 320, 1920);
                font-weight: 900;
                margin: 10px 0;
            }
            .track{
                @extend %more;
                padding: calcFluid(2, 10, 320, 1920)  calcFluid(20, 45, 320, 1920);
                margin: auto auto 0 0;
            }
            .active{
              background: gray;
            }

            .block__inputRow{ 
                margin: auto 0 0 0;
                @extend %d-row;
            }

            .block__input{
                @extend %d-col;
                .fd-col{
                    margin: 0;
                    font-weight: 500;
                    color: #58585e;
                    font-size: calcFluid(10, 16, 320, 1920);
                    span{
                        font-size: calcFluid(12, 20, 320, 1920);
                        color: black;
                    }
                }
                .fd-col:nth-child(1){
                    margin: auto 0 10px 0;
                }
            }
            .block-col {
                margin: auto 0 0 auto;

                .block__price,
                .block__discount {
                    margin: 5px 0 0 0;
                }

                .block__discount {
                    font-size: calcFluid(10, 18, 320, 1920);
                    text-decoration: line-through;
                    color: #58585e;
                }
            }
        }
    }
    .userContent{
        box-shadow: 6px 6px 20px rgba(0, 0, 0, 0.25);
        @extend %d-row;
        padding: 10px;
        &__photo{
            width: 30%;
            border-radius: calcFluid(20, 71, 320, 1920);
            background: #D9D9D9;
            display: flex;
            flex-direction: row;
            img{
                margin: auto;
                max-width: 100%;
                max-height: 100%;
            }
        }
        .userContent__col{
            width: 70%;
            @extend %d-col;
            margin: 0 0 auto 20px;
            .userContent__name{
                font-size: calcFluid(16, 25, 320, 1920);
                font-weight: 900;
            }
            p{
                font-size: calcFluid(12, 19, 320, 1920);
            }
            .exit{
                @extend %more;
                margin: auto  auto 0 0;
            }
        }
    }
    .orders{
        @extend %d-col;
        h1{
            font-size: calcFluid(18, 30, 320, 1920);
        }
        .order__clear{
            text-align: center;
            margin: auto;
            h2, p{
                text-align: center;
             }
             h2{
                 font-size: calcFluid(17, 30, 320, 1920);
             }
             p{
                 font-size: calcFluid(12, 18, 320, 1920);
             }
        }
    }
}
@media(max-width: 730px) {
    .userHtml {
        .userContent{
            display: flex;
            flex-direction: column;
            .userContent__photo{
                width: 100%;
                height: 200px;
            }
            .userContent__col{
                display: flex;
                flex-direction: column;
                margin: 0;
                width: 100%;
                .userContent__row{
                    .rename{
                        margin: 0 ;
                    }
                }
            }
        }
        .bag__block{
            display: flex;
            flex-direction: column;
            .block__img, 
            .block__imgJS {
                max-width: 50%;
            }
        }
    }
}